.blog__container{
    grid-template-columns: repeat(3, 1fr);
    column-gap: 1.875rem;
}
.blog__card{
    box-shadow: var(--icon-color);
    border-radius: var(--border-radius);
    overflow: hidden;
}
.blog__thumb{
    position: relative;
    overflow: hidden;
}

.blog__img{
    transform: scale(1);
    transition: .3s;
}

.blog__category{
    color: white;
    background-color: var(--icon-color);
    border-bottom-left-radius: 0.9375rem;
    border-bottom-right-radius: 0.9375rem;
    position: absolute;
    top: 0;
    left: 1.25rem;
    font-size: medium;
    display: inline-block;
    padding: 0.125rem 0.5rem;
    transition: 0.3s;
    z-index: 1;
}

.blog__details{
    background-color: rgb(0, 0, 0);
    margin-bottom: 0.5rem;

}
.blog__meta{
    display: flex;
    column-gap:0.6rem ;
    font-size: medium;
    color: aquamarine;
}
.blog__dot{
    font-weight: bolder;
}
.blog__title{
    color: white;
}