.section_title {
    font-size: 1.875rem;
    text-align: center;
    font-weight: 900;
}

.btn {
    width: 150px;
    font-size: 16px;
    font-weight: 600;
    color: #fff;
    cursor: pointer;
    margin: 20px;
    height: 50px;
    text-align: center;
    border: none;
    background-size: 300% 100%;

    border-radius: 50px;
    -o-transition: all .4s ease-in-out;
    -webkit-transition: all .4s ease-in-out;
    transition: all .4s ease-in-out;
}

.btn:hover {
    background-position: 100% 0;
    -o-transition: all .4s ease-in-out;
    -webkit-transition: all .4s ease-in-out;
    transition: all .4s ease-in-out;
}

.btn:focus {
    outline: none;
}

.btn {
    background-image: linear-gradient(to right, #25aae1, #40e495, #30dd8a, #2bb673);
    box-shadow: 0 4px 15px 0 rgba(49, 196, 190, 0.75);
}



.about__containet {
    grid-template-columns: 3fr 9fr;
    column-gap: 1.87rem;
}

.about__data {
    padding: 1.87rem;
    box-shadow: var(--shadow);
    border-radius: var(--border-radius);
    grid-template-columns: repeat(2, 1fr);
    column-gap: 1.87rem;
    align-items: flex-start;
    position: relative;


}

.section_title {
    color: rgb(250, 249, 249);
    font-weight: bold;


}

.about__description {
    font-size: 1rem;
    margin-bottom: 1rem;
    text-align: justify;
    font-weight: bold;
    color: rgb(250, 249, 249);
    line-height: 22px;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;

}

.about__skills {
    row-gap: 1.25rem;
}

.skills__tittle {
    display: flex;
    justify-content: space-between;
    color: rgb(252, 252, 252);
}

.skills__name {
    font-size: 1rem;
    font-weight: bolder;
    color: var(--icon-color);

}

.skills__numbers {
    line-height: 2.2rem
}

.skills__bar,
.skills__percentage {
    height: 7px;
    border-radius: 0.23rem;
}

.skills__bar {
    background-color: rgb(247, 243, 238);
}

.skills__percentage {
    display: block;

}

.developments {
    width: 80%;
    background-color: yellow;

}

.css {
    width: 70%;
    background-color: rgb(115, 179, 78);

}

.react {
    width: 60%;
    background-color: rgb(19, 162, 206);

}

.python {
    width: 50%;
    background-color: rgb(238, 20, 104);

}




/* ========================AboutBox Css============================= */
.about__boxes {
    grid-template-columns: repeat(4, 1fr);
    column-gap: 0.875rem;
    margin-top: 4.5rem;
    color: white;

}

.about__box {
    display: flex;
    column-gap: 1.2rem;
}

.about__icon {
    font-size: 1.5rem;
    color: var(--icon-color);

}

.about__title {
    font-size: 1.875rem;

}

.about__subtitle {
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-weight: bold;
   

}


.about__img{
  margin-left: 12rem;
  width: 20%;
  height: auto;
  border: none;
  border-radius: 40px 10px; 
}

@media (max-width:460px){
  .about__container{
      margin-top:17rem;
  }
  .about__boxes {
      grid-template-columns:repeat(2, 1fr);
  }
  .about__data {
      padding: 1.87rem;
      box-shadow: var(--shadow);
      border-radius: var(--border-radius);
      grid-template-columns: repeat(1, 1fr);
      column-gap: 1.87rem;
      align-items: flex-start;
      position: relative;
  }
  .about__img{
      margin-top: -17rem;
      margin-left: 0rem;
      width: 30%;
      height: auto;
      border: none;
      border-radius: 1px 10px; 
    }
    .section_title {
      color: rgb(250, 249, 249);
      font-weight: bold;
      font-size: medium;
      margin-left:-2rem ;
  }
  .about__description {
      font-size: 0.8rem;
      margin-left: -4rem;
      margin-bottom: 0rem;
      margin-top: -13rem;
      
  }
  .btn{
      margin-left: -1rem;
      width: 150px;
    font-size: 16px;
    font-weight: 600;
    color: #fff;
    cursor: pointer;
    height: 50px;   
  }
  .about__boxes{
      margin-left: -2.7rem;
  }
  .about_skills{
      margin-left: -3.8rem;
  }
}