.aside-bar {
    position: fixed;
    left: 0;
    top: 0;
    padding: 2rem;
    width: 100px;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    z-index: 10;
    float: left;
}


.nav__lists {
    margin-top: 7rem;
    display: flex;
    flex-direction: column;
    row-gap: 0.5rem;
    display: flex;

}

.nav__link {
    font-size: 1.5rem;
    color: white;
    font-weight: 1rem;
}

.nav__link:hover {
    color: var(--icon-color);
}

.copyrights {
    color: white;
    font-size: 0.8rem;
    transform: rotate(-180deg);
    writing-mode: vertical-rl;
}

.nav__logos {
    padding-left: 0.1rem;
    width: 3.7rem;
    height: 1rem;
    margin-left: -1rem;
}

@media (max-width:460px) {
    .aside-bar {
        padding: 1rem;
    }
}