.home{
    position: relative;
    min-height: 100vh;
    display: flex;
}

.home__name{
    margin-right: 40rem;
    margin-top: 14rem;
    font-size:30px ;
    color: #fdfdfd;
    margin-left: 1rem;
    margin-bottom: 10px;
    font-weight: bold;
}
.home__name span{
    color:var(--icon-color);
}
.home__p{
    font-weight: bold;
    margin: 0 4rem ;
    color: rgb(250, 249, 249);
    line-height: 22px;
    font-family:system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    
    

}
.btn{
    width: 200px;
    font-size: 17px;
    font-weight: 600;
    color: #fff;
    cursor: pointer;
    margin-top: 1rem;
    margin: 20px;
    height: 55px;
    text-align:center;
    border: none;
    background-size: 300% 100%;

    border-radius: 50px;
    -o-transition: all .4s ease-in-out;
    -webkit-transition: all .4s ease-in-out;
    transition: all .4s ease-in-out;
}

.btn:hover {
    background-position: 100% 0;
    -o-transition: all .4s ease-in-out;
    -webkit-transition: all .4s ease-in-out;
    transition: all .4s ease-in-out;
}

.btn:focus {
    outline: none;
}

.btn{
    background-image: linear-gradient(to right, #25aae1, #40e495, #30dd8a, #2bb673);
    box-shadow: 0 4px 15px 0 rgba(49, 196, 190, 0.75);
    
}

.primary__btn:hover{
    box-shadow: 0 12px 16px 0 rgba(219, 26, 26, 0.24), 0 17px 50px 0 rgba(0,0,0,0.19);
    display:none;
}

.intro{
   margin-left: 14rem;
   border: 12px solid rgb(117, 9, 9);
   border-radius: 30px;

}

.social__media{
    margin-left: 3rem;
    padding: 10px;
    font-size: 25px;
    text-decoration: none;
    column-gap: 1.3rem;
    border-radius: 3rem;
   
}
.social__media:hover{
    box-shadow: 0 12px 16px 0 rgba(11, 100, 233, 0.24), 0 17px 50px 0 rgba(253, 229, 13, 0.19);
}

.home_socials-link{
    color:rgb(156, 24, 24);
    
}
.home_socials-link1{
    color: rgb(66 103 178);
}
.home_socials-link2{
    color:rgb(2, 243, 203);
}




.home__image{
    width: 44%;
    height: 6%;
    bottom:0;
    right: 100px;
    margin-left: 40rem;
    margin-top: -29rem;
    display: flex;
}


@media (max-width:460px) {

    .home__p{
        margin-left: 2rem;
        font-size: 1rem;
    }
    .home__container{
        margin-top: 18rem;
        margin-left: -16rem;
    }
    .home__name{
       margin-top: 4rem;
       font-size: 1rem;
    }
    .intro{
        margin-top: 2rem;
        border:none;
        border-radius: 30px;
     
     }
     .home__image{
        margin-top: -35rem;
        margin-left: 15rem;
    }
    .btn{
        margin-left: 23rem;
    }

}