.services__container{
    grid-template-columns: repeat(3, 1fr);
    column-gap:1.875rem;
}

.services__card{
    text-align: center;
    padding: 1.875rem;
    border-radius: 14px  ;
    background-color: var(--icon-color);
    transition: all 0.3s ease-in-out;
}
.services__card:hover{
    transform: translateY(-17px);
}

.services__card:nth-child(1){
    /* background-color: rgb(33, 63, 230); */
    box-shadow: 0px 5px 20px 0px rgb(204, 221, 129 / 50%);
}
.services__card:nth-child(1){
    /* background-color: rgb(41, 95, 211); */
    box-shadow: 0px 5px 20px 0px rgb(204, 221, 129 / 50%);
}
.services__card:nth-child(1){
    /* background-color: rgb(78, 139, 209); */
    box-shadow: 0px 5px 20px 0px rgb(204, 221, 129 / 50%);
}

.services__img{
    margin-bottom: 1.25rem;
}
.services__title{
    font-size: large;
    margin-bottom: 1rem;
    color: white;
    font-weight: bold;
}
.services__description{
    font-family:system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-weight: bold;
    color: black;
}

.services__card:nth-child(2) .services__title{
    color: var(--icon-color);
}

.section__title{
    color: white;
    text-align: center;
    font-size: 1.875rem;
    font-weight: 900;
    
}

@media (max-width:460px) {
    .services__container{
        grid-template-columns: repeat(1, 1fr);
        column-gap:1.7rem;
        margin-left: -1.9rem;
    }
    .services__card{
        
        text-align: center;
        padding: 0.875rem;
        border-radius: 22px  ;
        background-color: var(--icon-color);
    }
}