.resume_container{
    grid-template-columns: repeat(2, 1fr);
}

.timeline{
    /* background-color: aliceblue; */
    padding: 1.875rem;
    border-radius: var(--border-radius);
    position: relative;
    box-shadow: var(--shadow);

}
.timeline__item{
    position: relative;
    padding-left: 3.25rem;
    padding-bottom: 3.125rem;

}
.timeline__item:last-child{
    padding-bottom: 0;
    
}

.timeline__item::before{
    content: '';
    /* width: 4px ; */
    height: 100%;
    background-color: yellow;
    position: absolute;
    left: .25rem;
}


.timeline .icon-graduation{
    position: absolute;
    left: -0.7rem;
    top: -0.5rem;
    font-size: 2rem;
    color: rgb(160, 211, 93);
    /* background-color: red; */
    padding: 1rem 0;
}
.timeline .icon-briefcase{
    position: absolute;
    left: -0.7rem;
    top: -0.5rem;
    font-size: 2rem;
    color: rgb(93, 211, 195);
    /* background-color: red; */
    padding: 1rem 0;
}

.timeline__date{
    color: rgb(179, 202, 127);
    font-size: 1.5rem;
    font-weight: bolder;

}

.timeline__title{
    font-size:1.3rem;
    margin: 0.5rem 0;
    color: yellow;
    font-weight: 300px;
    font-family:system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    
}
.timeline{
    color: rgb(245, 245, 245);
}

.timeline__text{
    font-family:system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-weight: bold;
}

